import Vue from 'vue'
import Router from 'vue-router'
import Personal from './personal'

Vue.use(Router)



let RouteList = [
  {
    path: '/',
    component: resolve => require(['@/views/layout/App.vue'], resolve),
    meta: {
      title: '首页',
      keepAlive: false,
    },
    children: [
      {
        path: '/',
        name: 'Dashboard',
        meta: {
          title: '首页',
          keepAlive: true
        },
        component: resolve => require(['@/views/home/Index.vue'], resolve),
      },
      {
        path: '/role_manage',
        name: 'RoleManage',
        meta: {
          title: '角色列表',
          keepAlive: true
        },
        component: resolve => require(['@/views/permissionManage/role/Role.vue'], resolve),
      },
      {
        path: '/permission_list',
        name: 'PermissionList',
        meta: {
          title: '权限列表',
          keepAlive: true
        },
        component: resolve => require(['@/views/permissionManage/permission/Index.vue'], resolve),
      },
      // 客服管理模块
      {
        path: '/customer_orders',
        name: 'Customer_orders',
        meta: {
          title: '用户详情',
          keepAlive: true
        },
        component: resolve => require(['@/views/customer/customerOrders'], resolve),
      },
      {
        path: '/report_protocol',
        name: 'Report_protocol',
        meta: {
          title: '报告协议详情',
          keepAlive: true
        },
        component: resolve => require(['@/views/customer/reportAndProtocol'], resolve),
      },
      {
        path: '/report',
        name: 'Report',
        meta: {
          title: '报告协议详情',
          keepAlive: true
        },
        component: resolve => require(['@/views/customer/report'], resolve),
      },
      {
        path: '/fetch',
        name: 'Fetch',
        meta: {
          title: '代扣跑批',
          keepAlive: true
        },
        component: resolve => require(['@/views/customer/withholdFetch'], resolve),
      },
      //个人中心，可能有修改密码，头像修改等路由
      Personal.index,
    ]
  },
  {
    path: '/login',
    name: 'Login',
    meta: {
      title: '后台登录',
      keepAlive: false
    },
    components: {
      blank: resolve => require(['@/views/login/Login.vue'], resolve),
    }
  },

]

export default new Router({
  // mode: 'history',
  routes: RouteList
})





